


















































import HeadTtop from '../../../components/head/head.vue'

export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '认证资料',
			backshow: true,
			topsrc: '../../../static/images/back.png',
			real_username: '',
			email: '',
			id_card: '',
			id_card_face: '',
			id_card_con: '',
			hold_id_card: ''
		}
	},
	methods: {
		SureClick() {
			var that = this;
			var pattern = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
			if (that.real_username === '') {
				uni.showToast({
					title: '请输入真实姓名',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (!pattern.test(this.email)) {
				uni.showToast({
					title: '请输入正确邮箱',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.email === '') {
				uni.showToast({
					title: '请输入邮箱',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.id_card === '') {
				uni.showToast({
					title: '请输入身份证号码',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.id_card.length == 18) {

			} else {
				uni.showToast({
					title: '请输入正确身份证号码',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.id_card_face === '') {
				uni.showToast({
					title: '请上传身份证反面',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.id_card_con === '') {
				uni.showToast({
					title: '请上传身份证反面',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}
			if (that.hold_id_card === '') {
				uni.showToast({
					title: '请上传手持身份证照片',
					icon: 'success',
					mask: true,
					duration: 1000
				});
				return false
			}

			let params = {
				_token: uni.getStorageSync('userinfo')._token,
				email: this.email,
				real_username: this.real_username,
				id_card: this.id_card,
				id_card_face: this.id_card_face,
				id_card_con: this.id_card_con,
				hold_id_card: this.hold_id_card,
			}
			this.$http.HttpRequst.request(false, 'user/identityAuth', params, 'POST', res => {

				//console.log(res)

				uni.showToast({
					title: res.msg,
					duration: 1000,
					success() {
						setTimeout(function() {
							uni.navigateBack();
						}, 1000)
					}
				});
			});

		},
		id_card_faceClick() {
			var that = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					let params = {
						_token: uni.getStorageSync('userinfo')._token
					}
					that.$http.HttpRequst.requestUploadFile(true, 'index/upload', params, res.tempFilePaths, res => {
						that.id_card_face = res.data.img1
					});
				}
			});
		},
		id_card_conClick() {
			var that = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					let params = {
						_token: uni.getStorageSync('userinfo')._token
					}
					that.$http.HttpRequst.requestUploadFile(true, 'index/upload', params, res.tempFilePaths, res => {
						that.id_card_con = res.data.img1
					});
				}
			});
		},
		hold_id_cardClick() {
			var that = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					let params = {
						_token: uni.getStorageSync('userinfo')._token
					}
					that.$http.HttpRequst.requestUploadFile(true, 'index/upload', params, res.tempFilePaths, res => {
						that.hold_id_card = res.data.img1
					});
				}
			});
		}
	}
}
